@use "./common/variables.scss";

.form-page {
  padding-top: 39px;
}

.form-page__caption {
  text-align: center;
  margin-bottom: 24px;
  padding-left: 20px;
  padding-right: 20px;
}

.form-page__text {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 27px;
  border-bottom: 1px solid #e5e5e5;
}

@media (min-width: variables.$tablet-width) {
  .form-page {
    padding: 66px 60px 37px;
  }

  .form-page__caption {
    margin-bottom: 20px;
  }

  .form-page__text {
    width: 478px;
    margin: 0 auto 40px;
    border-bottom: none;
  }

  .form-page__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: variables.$desktop-width) {
  .form-page {
    padding: 117px 120px 0 120px;
  }

  .form-page__caption {
    margin-bottom: 32px;
  }

  .form-page__text {
    margin-bottom: unset;
  }
}
