@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.page {
  min-height: 100%;
}

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  font-weight: 400;
  min-width: 320px;
  min-height: 100vh;
}

.body__container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.body__main {
  flex-grow: 1;
}

@media (min-width: 1200px) {
  .body {
    background-color: #f2f2f2;
  }

  .body__container {
    max-width: 1200px;
    margin: 0 auto;
    background-color: #ffffff;
    box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (max-width: 767px) {
  .toggle {
    display: none;
    position: absolute;
    width: 23px;
    height: 22px;
    border: none;
    margin: 0;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
  }
  .nav--closed .toggle {
    display: block;
  }
  .toggle::before {
    content: "";
    display: block;
    width: 100%;
    height: 4px;
    background-color: #81b3d2;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  }
  .toggle:hover, .toggle:focus, .toggle:focus-visible {
    opacity: 0.6;
    outline: none;
  }
  .toggle:active {
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .toggle {
    display: none;
  }
}
.nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #81b3d2;
  position: relative;
  z-index: 3;
  top: 0;
  left: 0;
  right: 0;
}

.nav-list__item {
  box-shadow: 0 1px 0 0 rgba(1, 1, 1, 0.2), 0 2px 0 0 rgba(255, 255, 255, 0.2) inset;
  text-align: center;
  padding: 18px 52px 12px 52px;
}
.nav-list__item:last-child {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (max-width: 767px) {
  .nav-list__item:hover, .nav-list__item:focus {
    background-color: #669ec0;
  }
  .nav-list__item:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
}

.nav-list__item-link {
  color: inherit;
  display: block;
}

@media (min-width: 768px) {
  .nav-list {
    position: static;
    display: grid;
    background-color: #ffffff;
    grid-template-columns: 1fr 1fr 152px 1fr 1fr;
    grid-template-areas: "a b x c d";
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    /* 150% */
    text-transform: uppercase;
  }

  .nav-list__item:nth-child(4n-1) {
    grid-column-start: c;
  }

  .nav-list__item {
    box-shadow: none;
    padding: 18px 18px 12px;
  }
  .nav-list__item--current {
    color: #766357;
  }
  .nav-list__item:hover, .nav-list__item:focus {
    color: #81b3d2;
  }
  .nav-list__item:active {
    opacity: 0.3;
  }
  .nav-list__item:last-child {
    background: none;
  }
}
@media (min-width: 1200px) {
  .nav-list {
    grid-template-columns: 1fr 1fr 240px 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .nav--opened .toggle {
    width: 24px;
    height: 24px;
    right: 19px;
    top: 14px;
    display: block;
    z-index: 3;
    padding: 15px;
  }
  .nav--opened .toggle::before, .nav--opened .toggle::after {
    content: "";
    display: block;
    width: 26px;
    height: 4px;
    background-color: #ffffff;
    position: absolute;
    left: calc(50% - 12px);
    top: calc(50% - 2px);
    box-shadow: none;
  }
  .nav--opened .toggle::before {
    transform: rotate(45deg);
  }
  .nav--opened .toggle::after {
    transform: rotate(-45deg);
  }

  .nav--opened .nav-list {
    display: block;
    position: absolute;
    z-index: 3;
    width: 100%;
  }

  .nav--closed .nav-list {
    display: none;
  }

  .nav {
    position: relative;
  }

  .nav__button {
    right: 20px;
    top: 18px;
  }
}
