.advantages-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  counter-reset: number;
  position: relative;
}

.advantages-list__break {
  display: none;
}

.advantages-list__text-wrapper {
  padding: 25px 14px 23px;
}

.advantages-list__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.advantages-list__caption {
  text-align: center;
  color: inherit;
}
.advantages-list__caption::after {
  content: "– №" counter(number) " –";
  counter-increment: number;
  left: 50%;
  display: block;
  width: 100px;
  height: 30px;
  font-size: 14px;
  font-weight: 400;
  color: inherit;
  margin: 0 auto 12px;
}

.advantages-list__text {
  color: inherit;
  max-width: 267px;
  margin: 0 auto;
}

.advantages-list__item--box {
  position: relative;
  padding: 47px 0 0 0;
}
.advantages-list__item--box::after {
  content: "";
  width: 100%;
  height: 37px;
  display: block;
  background: url("../img/icons/sprite.svg#advantage-triangle") no-repeat center;
  position: absolute;
  top: -1px;
  left: 0;
  right: 0;
  background-size: cover;
}
.advantages-list__item--box > .advantages-list__text-wrapper {
  padding-bottom: 37px;
}
.advantages-list__item--container {
  padding-top: 12px;
}
.advantages-list__item--container > .advantages-list__text-wrapper {
  padding-bottom: 37px;
}
.advantages-list__item:nth-child(n+3) {
  background-color: #eeeeee;
  padding: 0 18px;
  color: #333333;
}
.advantages-list__item:nth-child(-n+2) {
  background-color: #81b3d2;
  color: #ffffff;
}
.advantages-list__item:nth-child(n+3):not(:last-child)::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #d6d6d6;
}

.advantages-list__item:last-child .advantages-list__text-wrapper {
  padding-bottom: 29px;
}

.advantages-list__row {
  display: block;
  margin-bottom: 7px;
}

.advantages-list__img-wrapper {
